.cursor{
    width: 3px;
    height: 30px;
    background-color: black;
    animation: blink .75s step-end infinite;
}

@keyframes blink {
	from, to { 
		background-color: transparent 
	}
	50% { 
		background-color: black; 
	}
}

.typeAndDeleteComp{
    display: flex;
    flex-direction: row;
}

.text{
    font-size: 25px;
    font-weight: bold;
    font-family: 'Times New Roman', Times, serif;
}